.product {
    display: flex;
    flex-direction: column;
    /* border: 1px solid var(--tg-theme-button-color); */
    padding: 15px;
    width: 170px;
}

.img {
    width: 100%;
    background: lightgray;
    height: 100px;
    margin: 0 auto;
    background: var(--tg-theme-button-color);
}

.description {
    font-size: 0.8em;
    color: var(--tg-theme-hint-color);
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis; 
}


.button-section {
    flex-direction: row;
    margin-top: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.add-btn {
    border-radius: 10px;
    width: 36px;
    height: 36px;
    order: 3;
    text-align: center;
}

.subtract-btn {
    border-radius: 10px;
    width: 40px;
    height: 40px;
    order: 1;
}

.quantity {
    order: 2;
}

counter {
    flex-direction: row;
    margin: 25px;
}

.title {
    font-size: 1em;
    white-space: nowrap;
    overflow-y: hidden;
    overflow-x: hidden;
    text-overflow: ellipsis;
}


.big_button{
    width: 140px;
}

.anchors {
    white-space: nowrap;
    overflow-x: scroll;
}