.list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.item {
    margin-right: 10px;
    margin-bottom: 10px;
}

.section{
    font-size: 1.8em;
    padding: 10px;
    font-weight: bold;
}

.section_description {
    padding: 10px;
    font-size: 0.8em;
    color: var(--tg-theme-hint-color);
}

.bigDescription {
    font-size: 1em;
    color: var(--tg-theme-hint-color);
    padding: 10px;
}

.bigTitle {
    font-size: 1.6em;
    padding: 10px;
    font-weight: bold;
}

.bigImg {
    width: 100%;
    background: lightgray;
    height: auto;
    margin: 0 auto;
    background: var(--tg-theme-button-color);
    align-items: center;
}

.anchors {
    padding: 10px;
    position: fixed;
    background-color: var(--tg-theme-bg-color);
    width: 100%;
    overflow: scroll;
}

.anchor {
    padding-left: 10px; 
    padding-right: 10px; 
    color: white;
    background-color: var(--tg-theme-button-color);
    border-radius: 10px;
    text-decoration: none;
    font-size: 1.2em;
    margin-right: 20px;
}

.anchor:visited {
    padding-left: 10px; 
    padding-right: 10px; 
    color: white;
    background-color: var(--tg-theme-button-color);
    border-radius: 10px;
    text-decoration: none;
}

.top {
    padding-top: 50px;
}