.profile_picture {
    border-radius: 100%;
}

.profile_pic_container {
    display: flex;
    justify-content: center;
    padding: 10px;
  }

  .profile_picture {
    width: 150px;
    height: 150px;
    margin-bottom: 50px;
    margin-top: 50px;
  }

  .profile_textblock {
    padding: 10px;
    text-align: center;
    width: 350px;
    /* border: 1px solid var(--tg-theme-button-color); */
  }

  .left {
    color: var(--tg-theme-text-color);
    float: left;
    width: 50%;
    text-align: left;
  }
  
  .right {
    color: var(--tg-theme-button-color);
    float: right;
    width: 50%;
    text-align: right;
  }

  .profile_overall {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
  }