.list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.item {
    margin-right: 10px;
    margin-bottom: 10px;
}

.section{
    font-size: 1.8em;
    padding: 10px;
    font-weight: bold;
}

.section_description {
    padding: 10px;
    font-size: 0.8em;
    color: var(--tg-theme-hint-color);
}

.bigDescription {
    font-size: 1em;
    color: var(--tg-theme-hint-color);
    padding: 10px;
}

.bigTitle {
    font-size: 1.6em;
    padding: 10px;
    font-weight: bold;
}

.bigImg {
    width: 100%;
    background: lightgray;
    height: auto;
    margin: 0 auto;
    background: var(--tg-theme-button-color);
    align-items: center;
}

.top {
    padding-top: 50px;
}