.centralize {
    display: flex;
    justify-content: center;
}

.contacts_textblock {
    padding: 10px;
    text-align: center;
    width: 350px;
    /* border: 1px solid var(--tg-theme-button-color); */
  }

.left {
    color: var(--tg-theme-text-color);
    float: left;
    width: 50%;
    text-align: left;
    }
  
.right {
    color: var(--tg-theme-button-color);
    float: right;
    width: 50%;
    text-align: right;
  }

.address{
    margin-bottom: 10px;
    font-weight: bold;
    margin-left: 40px;
  }

.contacts_description {
    font-size: 0.8em;
    color: var(--tg-theme-hint-color);
    margin: 5px;
    }

.contacts_overall {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    }

.Bold{
    font-weight: bold;
}

.logo {
    display: flex;
    align-items: center;
    height: auto;
    margin: 0 auto;
    width: 150px;
    padding-bottom: 40px;
}