.product_basket {
    display: flex;
    flex-direction: row;
    /* border: 1px solid var(--tg-theme-button-color); */
    padding: 15px;
    width: 100%;
    justify-content: space-between;
    align-items: center;
}

.basket_img {
    width: 30%;
    background: lightgray;
    height: auto;
    background: var(--tg-theme-button-color);
    float: left;
    order: 1;
    margin: 0%;
}

.description {
    font-size: 0.8em;
    color: var(--tg-theme-hint-color);
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis; 
}


.button-section {
    flex-direction: row;
    margin-top: 10px;
    display: flex;
    justify-content: space-between;
}

.basket_counter {
    order: 3;
}

.basket_title {
    width: 200px;
    font-size: 1em;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    order: 2;
}


.big_button{
    width: 140px;
}

.anchors {
    white-space: nowrap;
    overflow-x: scroll;
}

.right_part{
    float: right;
    display: flex;
    flex-direction: column;
}